            <Modal v-if="showTransferControlModal" :type="'transferControl'" :sponsorNickname="sponsorNickname" :sponsorSponsorNickname="sponsorSponsorNickname" :step="transferControlStep" @updateModal="updateModal" @closeModal="closeModal"/>
