<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'

import Modal from "@/components/modals/Modal.vue"
import AccountControlProfileCard from "@/components/AccountControlProfileCard.vue"
import LangsDropdownTrigger from "@/components/app/langs/LangsDropdownTrigger.vue"
import LangsDropdown from "@/components/app/langs/LangsDropdown.vue"
import Avatar from "@/components/Avatar.vue"
import globalConfig from '@/globalConfig'

export default defineComponent({
    data:  () => ({
        user: {
        },
        isEditing: false,
        showDropdown: false,
        transferControlStep: 1,
        showTransferControlModal: false,
        // FINANCIAL PASSWORD CHANGE
        // financialPasswordAction: 'add', // 'add', 'edit'
        showChangeFinancialPasswordModal: false, 
        showFinancialPasswordConfirmationModal: false,
        newFinancialPassword: null
        //
    }),
    components: {
        Avatar, LangsDropdownTrigger, LangsDropdown, Modal, AccountControlProfileCard
    },
    methods: {
        requestChangeFinancialPasswordSuccessful(password) {
            this.showChangeFinancialPasswordModal = false
            this.newFinancialPassword = password
            setTimeout(()=> {
                this.showFinancialPasswordConfirmationModal = true
            }, 0)
        },
        async closeModals() {
            this.showTransferControlModal = false
            this.transferControlStep = 1

            this.showChangeFinancialPasswordModal = false
            this.showFinancialPasswordConfirmationModal = false
            this.newFinancialPassword = null
            await this.$store.dispatch('getProfile')
        },
        triggerDropdown() {
            this.showDropdown = !this.showDropdown
        },
        closeDropdown() {
            this.showDropdown = false
        },
        forceUpdateAll() {
            this.$emit('forceUpdateAll')
        },

        giveControl(){
            this.showTransferControlModal = true
        },
        async cancelControl(){
            try {
                await this.$store.dispatch('cancelManagementByClient')
                this.$message(localizeFilter('profile_updated'))
            } catch(e) {
                this.$error(e)
            } 
        },
        async cancelControlRequest(){
            try {
                await this.$store.dispatch('cancelManagementRequest')
                this.$message(localizeFilter('profile_updated'))
            } catch(e) {
                this.$error(e)
            } 
        },
        updateModal(step, transferControlNickname) {
            this.transferControlStep=step
            if(step == 3) {
                this.closeModals()
                this.createManagementRequest(transferControlNickname)
            }
        },

        async createManagementRequest(managerNickname){
            const formData = {managerNickname}
            try {
                await this.$store.dispatch('createManagementRequest', formData)
                this.$message(localizeFilter('profile_updated'))
            } catch(e) {
                this.$error(e)
            } 
        },

        startEditing() {
            this.isEditing = true
        },
        async endEditing() {
            try {
                const formData = {firstName: this.user.firstName, telegramName: this.user.telegramName, lastName: this.user.lastName, nickname: this.user.nickname}
                await this.$store.dispatch('editProfile', formData)
                this.$message(localizeFilter('profile_updated'))
                this.isEditing = false
            } catch(e) {
                this.$error(e)
            } 
        },
         copy(value) {
            function fallbackCopyTextToClipboard(text) {
                var textArea = document.createElement("textarea");
                textArea.value = text;
                
                // Avoid scrolling to bottom
                textArea.style.top = "0";
                textArea.style.left = "0";
                textArea.style.position = "fixed";

                document.body.appendChild(textArea);
                textArea.focus();
                textArea.select();
                var successful;
                try {
                    successful = document.execCommand('copy');
                    var msg = successful ? 'successful' : 'unsuccessful';
                    console.log('Fallback: Copying text command was ' + msg);
                } catch (err) {
                    console.error('Fallback: Oops, unable to copy', err);
                }

                document.body.removeChild(textArea);
                return successful;
            }
            if (value) {
                const clipboardData =
                // event.clipboardData ||
                window.clipboardData ||
                // event.originalEvent?.clipboardData ||
                navigator.clipboard;
                try {
                    clipboardData.writeText(value); 
                    this.$message(localizeFilter('copied')) 
                } catch (e) {
                    if(fallbackCopyTextToClipboard(value)){
                        this.$message(localizeFilter('copied')) 
                    }
                }
            }
      }
    },
    computed: {
        avatarLink(){
            return this.$store.state.info.profile ? this.$store.state.info.profile.avatarLink : null;
        },
        sponsorNickname(){
            return this.$store.getters.profile.managedData.sponsorNickname
        },
        sponsorSponsorNickname(){
            return this.$store.getters.profile.managedData.sponsorSponsorNickname
        },
        managerNickname(){
            return this.$store.getters.profile.managedData.managerNickname
        },
        isPending(){
            return this.$store.getters.profile.managedData.isPending
        },
        country(){
            return this.$store.getters.countryByCode[this.$store.getters.profile.countryCode]
        },
        sex(){
            return this.user.sex ? localizeFilter(this.user.sex) : ''
        },
        financialPasswordAction(){
            return this.$store.getters.profile.financialPasswordSet ? 'edit' : 'add'
        },
    },
    async mounted() {
        this.$emit('mountedChild', 'Profile');
        try {
            const user = await this.$store.dispatch('getProfile')
            user.referralLink=`${globalConfig.getHostName()}/#/?token=${user.referralToken}`
            this.user = user
        } catch(e) {
            this.$error(e)
        } 
    }
})
</script>

